import { useQuery } from 'react-query'
import { Card, CardBody, CardTitle, CardText, Container, Row, Col } from 'reactstrap'
import { useParams } from 'react-router'
import { Link } from 'react-router'

import { httpGet } from './utils/axiosUtils'

function Course() {
  const params = useParams()
  const { isLoading, error, data } = useQuery({
    queryKey: ['course'],
    queryFn: async () => await httpGet(`courses/${params.courseId}`)
  })

  if (isLoading) return (<>{'Loading...'}</>)
  if (error) return (<>{'An error has occurred: ' + error}</>)

  const courseContents = (
    data?.contents?.map(({ _id, title, description }: any) => {
      return (
        <Row key={title}>
          <Col>
            <Card style={{width: '100%'}}>
              <CardBody>
                <CardTitle tag="h5">
                  <Link to={`${_id}`}>{title}</Link>
                </CardTitle>
                <CardText>
                  {description}
                </CardText>
              </CardBody>
            </Card>
          </Col>
        </Row>
      )
    })
  )

  return (
    <Container>
      <Row>
        <h2>
          Course
        </h2>
      </Row>
      <Row>
        <Col>{data.title}</Col>
      </Row>
      <Row>
        <Col>{data.author}</Col>
      </Row>
      <Row>
        <Col><pre>{data.summary}</pre></Col>
      </Row>
      <Row>
        <Col><pre>{data.description}</pre></Col>
      </Row>
      {courseContents}
    </Container>
  )
}

export default Course
