import { useState, useEffect } from 'react'
import { useQuery } from 'react-query'
import { 
  Card, 
  CardBody, 
  CardTitle, 
  Container, 
  Row, 
  Col, 
  CardSubtitle,
  FormGroup,
  Label,
  Input
} from 'reactstrap'
import { Link } from 'react-router'
import { v4 as uuid } from 'uuid'

import { httpGet, httpPost } from './utils/axiosUtils'
import InMemoryPagination from './inMemoryPagination'

type CourseFilters = { author?: string, subject?: string, skillLevel?: string, category?: string }
type CategoryState = { categoryDropDown?: any, categories: string[] }

function CourseList() {
  let authorsDropDown, subjectsDropDown, skillLevelsDropDown
  const [categoryState, setCategoryState] = useState<CategoryState>({ categoryDropDown: undefined, categories: [] })
  const pageSize = 9
  const [currentPage, setCurrentPage] = useState(1)
  const [maxPages, setMaxPages] = useState(1)
  const [selectedFilters, setSelectedFilters] = useState<CourseFilters>({ author: undefined, subject: undefined, skillLevel: undefined, category: undefined })
  const [courseCards, setCourseCards] = useState()
  const { isLoading, error, data } = useQuery({
    queryKey: [`courseList`],
    queryFn: async () => await httpGet('courses')
  })

  const { isLoading: isFilterDataLoading, error: filterError, data: filterData } = useQuery({
    queryKey: [`courseFilterValues`],
    queryFn: async () => await httpGet('courses/filterValues')
  })

  const enroll = async (courseId: string) => {
    await httpPost(`userCourses/enroll/${courseId}`, {})
  }

  useEffect(() => {
    if (!isLoading) {
      setCourseCards(createCourseCards())
    }
  }, [data, currentPage, selectedFilters])

  const createFilter = (label: string, field: string, values: string[]) => {
    const options = values.map((value) => <option>{value}</option>)
    return (
      <FormGroup>
        <Label for={`${field}Select`}>
          {label}
        </Label>
        <Input 
          id={`${field}Select`} 
          name={`${field}Select`} 
          type="select" 
          value={selectedFilters[field as keyof CourseFilters]} 
          onChange={(e) => updateFilters(e.target.value, field)}
        >
          {options}
        </Input>
      </FormGroup>
    )
  }

  const createCourseCards = () => {
    const startIndex = (currentPage - 1) * pageSize
    const endIndex = startIndex + pageSize
    if (!selectedFilters.author && !selectedFilters.subject) {
      return []
    }

    const filteredData = data?.filter((course: { author: string; subject: string; skillLevel: string; subCategory: string }) => 
      (!selectedFilters.author || course.author === selectedFilters.author)
      && (!selectedFilters.subject || course.subject === selectedFilters.subject)
      && (!selectedFilters.skillLevel || course.skillLevel === selectedFilters.skillLevel)
      && (!selectedFilters.category || course.subCategory === selectedFilters.category)
    )

    if (selectedFilters.subject) {
      const allCategories: string[] = filteredData.map(({ subCategory }: { subCategory: string }) => subCategory)
      const categories = ['', ...Array.from(new Set(allCategories))]
      if (categoryState.categories !== categories) {
        setCategoryState({ categoryDropDown: createFilter('Category', 'category', categories), categories: categories })
      }
    }

    setMaxPages(!filteredData.length ? 1 : Math.ceil(filteredData.length / pageSize))
    return (
      filteredData.slice(startIndex, endIndex).map(({ _id, title, author, subject, skillLevel, subCategory, thumbnails }: any) => {
        return (
          <Col sm="4" key={_id}>
            <Card style={{width: '322px'}}>
              <img alt="Sample" src={thumbnails?.medium?.url} height={thumbnails?.medium?.height} width={thumbnails?.medium?.width} />
              <CardBody>
                <CardTitle tag="h5">
                  {title}
                </CardTitle>
                <CardSubtitle>
                  <div>{author}</div>
                  <div>{subject}</div>
                  <div>{subCategory}</div>
                  <div>{skillLevel}</div>
                </CardSubtitle>
                <Link to={`/courses/${_id}`}>Explore</Link> 
                <span> | </span>
                <Link to='' onClick={() => enroll(_id)}>Enroll</Link>
              </CardBody>
            </Card>
          </Col>
        )
      })
    )
  }

  const updateFilters = (value: string, filter: string) => {
    const updatedFilters = selectedFilters
    updatedFilters[filter as keyof CourseFilters] = value
    if (filter === 'subject') {
      updatedFilters.category = ''
    }

    setSelectedFilters(updatedFilters)
    setCourseCards(createCourseCards())
  }

  if (isLoading) return (<>{'Loading...'}</>)
  if (error) return (<>{'An error has occurred: ' + error}</>)

  if (!isFilterDataLoading && !filterError) {
    authorsDropDown = createFilter('Authors', 'author', filterData.authors)
    subjectsDropDown = createFilter('Subjects', 'subject', filterData.subjects)
    skillLevelsDropDown = createFilter('Skill Levels', 'skillLevel', filterData.skillLevels)
  }

  const onFirstPageClick = () => {
    setCurrentPage(1)
  }

  const onPreviousPageClick = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1)
    }
  }

  const OnNextPageClick = () => {
    if (currentPage < maxPages) {
      setCurrentPage(currentPage + 1)
    }
  }

  const OnLastPageClick = () => {
    setCurrentPage(maxPages)
  }

  return (
    <Container>
      <Row>
        <h2>
          Courses
        </h2>
      </Row>
      <Row>
        <Col>{authorsDropDown}</Col>
        <Col>{subjectsDropDown}</Col>
        <Col>{skillLevelsDropDown}</Col>
      </Row>
      <Row>
        <Col></Col>
        <Col>{categoryState.categoryDropDown}</Col>
        <Col></Col>
      </Row>
      <Row>
        {courseCards}
      </Row>
      <Row>
        <Col>
          <InMemoryPagination 
            onFirstPageClick={onFirstPageClick}
            onPreviousPageClick={onPreviousPageClick}
            OnNextPageClick={OnNextPageClick}
            OnLastPageClick={OnLastPageClick}
          />
        </Col>
      </Row>
    </Container>
  )
}

export default CourseList
