import { useRef, useState, useEffect } from 'react'
import { Container, Row, Col, Button } from 'reactstrap'
import "./slides.css";

type SlideImage = {
  image: string
  imageText: string
}

type Slide = {
  title: string
  subTitle: string
  bulletPoints: string[]
  script: string
  images: SlideImage[]
  codeLines: string[]
}

type SlidesProps = {
  slides: Slide[]
}

function Slides({ slides }: SlidesProps) {
  const [started, setStarted] = useState(false)
  const [opacity, setOpacity] = useState(1)
  const [currentScriptSentence, setCurrentScriptSentence] = useState('')
  const [startButtonText, setStartButtonText] = useState('Start')
  const [currentSlideIndex, setCurrentSlideIndex] = useState(0)
  const [activeControlPanelButton, setActiveControlPanelButton] = useState('Start')
  const defaultCanvasHeight = 500
  const canvasRef = useRef(null)
  const scratchCanvasRef = useRef(null)

  useEffect(() => {
    if (activeControlPanelButton === 'Next') {
      onNextClick()
    }
  }, [activeControlPanelButton])

  const capitalizeWords = (str: string) => {
    return str.split(' ').map(word => word.charAt(0).toUpperCase() + word.slice(1)).join(' ')
  }

  const refreshSpeech = () => {
    setTimeout(() => {

    }, 10)
  }

  const wrapText = (context: any, text: string, x: number, y: number, maxWidth: number, lineHeight: number, font: string, isCentered: boolean) => {
    const words = text.split(' ')
    let line = ''
    context.fillStyle = "#000"
    context.font = font
    if (isCentered) {
      context.textAlign = 'center'
      context.textBaseline = 'middle'
    } else {
      context.textAlign = 'left'
      context.textBaseline = 'top'
    }

    for (let i = 0; i < words.length; i++) {
      const testLine = line + words[i] + ' '
      const metrics = context.measureText(testLine)
      const testWidth = metrics.width

      if (testWidth > maxWidth && i > 0) {
        context.fillText(line, x, y)
        line = words[i] + ' '
        y += lineHeight
      } else {
        line = testLine
      }
    }

    context.fillText(line, x, y)
    return y
  }

  const fadeOut = async () => {
    return new Promise<void>((resolve) => {
      setTimeout(() => {
        const fadeOutInterval = setInterval(() => {
          setOpacity((prevOpacity) => {
            const newOpacity = prevOpacity - 0.2
            if (newOpacity <= 0) {
              clearInterval(fadeOutInterval)
              resolve()
              return 0
            }

            return newOpacity
          })
        }, 100)
      }, 750)
    })
  }

  const fadeIn = async () => {
    return new Promise<void>((resolve) => {
      setTimeout(() => {
        const fadeInInterval = setInterval(() => {
          setOpacity((prevOpacity) => {
            const newOpacity = prevOpacity + 0.2
            if (newOpacity >= 1) {
              clearInterval(fadeInInterval)
              resolve()
              return 1
            }

            return newOpacity
          })
        }, 100)
      }, 750)
    })
  }

  const playScriptSentence = (
    counter: number, 
    sentences: string[], 
    callback: () => void
  ) => {
    const sentence = sentences[counter]
    setCurrentScriptSentence(sentence)
    const isLastSentence = counter + 1 === sentences.length
    let onEndCallbackFunction
    if (isLastSentence) {
      onEndCallbackFunction = () => {
        setCurrentScriptSentence('')
        callback()
      }
    } else {
      onEndCallbackFunction = () => {
        setCurrentScriptSentence('')
        playScriptSentence(++counter, sentences, callback)
      }
    }

    window.speechSynthesis.cancel()
    const utterance = new SpeechSynthesisUtterance(sentence)
    utterance.lang = "en-US"
    utterance.rate = 1
    utterance.onend = onEndCallbackFunction
    utterance.onerror = (e) => console.error('onerror', e)
    utterance.onboundary = () => console.error('onboundary')
    utterance.onmark = () => console.error('onmark')
    utterance.onpause = () => console.error('onpause')
    window.speechSynthesis.speak(utterance)
  }

  const playScript = (text: string, callback: () => void) => {
    const sentences = text.split(/[,.;!?]\s+/) 
    playScriptSentence(0, sentences, callback)
  }
  
  const renderSlide = async (slide: Slide, ctx: any, canvas: any, isTitleSlide: boolean, canvasHeight: number = 0) => {
    const lineHeight = 30
    let startHeight = isTitleSlide ? 200 : lineHeight
    const bulletPointStartY = 70
    const leftMargin = 20
    const rightMargin = 20
    let image: any
    let imageHeight = 0

    if (canvasHeight) {
      canvas.setAttribute('height', canvasHeight + "px")
      ctx.clearRect(0, 0, canvas?.width, canvas?.height)
    }
  
    // Title
    wrapText(ctx, capitalizeWords(slide.title), isTitleSlide ? canvas?.width / 2 - 10 : leftMargin, startHeight, canvas?.width - rightMargin, isTitleSlide ? 45 : 35, isTitleSlide ? "bold 36px Arial" : "bold 24px Arial", isTitleSlide)
  
    startHeight = bulletPointStartY

    // Subtitle or points
    if (slide.bulletPoints) {
      slide.bulletPoints.forEach((point) => {
        const endHeight = wrapText(ctx, `- ${capitalizeWords(point)}`, leftMargin, startHeight, canvas?.width - rightMargin, lineHeight, "18px Arial", false)
        startHeight = endHeight + lineHeight
      })
    }

    if (slide.codeLines) {
      startHeight = startHeight + lineHeight
      slide.codeLines.forEach((codeLine) => {
        const endHeight = wrapText(ctx, `- ${codeLine}`, leftMargin, startHeight, canvas?.width - rightMargin, lineHeight, "18px Arial", false)
        startHeight = endHeight + lineHeight
      })
    }

    if (slide.images?.length) {
      for (const slideImage of slide.images) {
        if (slideImage.image) {
          image = new Image()
          image.onload = function() {
            ctx.drawImage(image, leftMargin, startHeight + imageHeight)
          }
          image.src = slideImage.image
          await image.decode()
          imageHeight = imageHeight + image.height

          if (slideImage.imageText) {
            wrapText(ctx, slideImage.imageText, leftMargin, startHeight + imageHeight + 10, canvas?.width - rightMargin, lineHeight, "16px Arial", false)
            imageHeight = imageHeight + 10 + lineHeight * 2
          }
        }
      }
    }

    if (!canvasHeight) {
      const totalHeight = lineHeight + imageHeight + startHeight
      const slideHeight = totalHeight > defaultCanvasHeight ? totalHeight : defaultCanvasHeight
      canvas.setAttribute('height', slideHeight + "px")
      ctx.clearRect(0, 0, canvas?.width, canvas?.height)
    }

    return canvas
  }
  
  const nextSlide = async (ctx: any, canvas: any, slideIndex: number) => {
    if (slides && slideIndex < slides.length) {
      await fadeOut()
      const slide = slides[slideIndex]
      const isTitleSlide = slideIndex === 0 || slideIndex === slides.length - 1
      const scratchCanvas = await renderSlide(slide, ctx, canvas, isTitleSlide)
      await renderSlide(slide, ctx, canvas, isTitleSlide, scratchCanvas.height)
      await fadeIn()
  
      // Play the script and move to the next slide
      playScript(slide.script, async () => {
        setActiveControlPanelButton('Next')
      })
    } else {
      setStarted(false)
      setStartButtonText('Start')
    }
  }

  const onStartClick = async () => {
    const canvas: any = canvasRef.current
    const ctx = canvas?.getContext('2d')
    if (!started && ctx) {
      window.speechSynthesis.cancel()
      setStarted(true)
      setStartButtonText('Pause')
      setCurrentSlideIndex(0)
      await nextSlide(ctx, canvas, 0)
    } else if (started && startButtonText === 'Pause') {
      window.speechSynthesis.pause()
      setStartButtonText('Resume')
    } else {
      setStartButtonText('Pause')
      window.speechSynthesis.resume()
    }
  }

  const onNextClick = async () => {
    if (currentSlideIndex + 1 < slides.length) {
      setActiveControlPanelButton('Start')
      setStartButtonText('Pause')
      const canvas: any = canvasRef.current
      const ctx = canvas?.getContext('2d')
      window.speechSynthesis.cancel()
      setCurrentSlideIndex(currentSlideIndex + 1)
      await nextSlide(ctx, canvas, currentSlideIndex + 1)
    }
  }

  const onPrevClick = async () => {
    if (currentSlideIndex > 0) {
      const canvas: any = canvasRef.current
      const ctx = canvas?.getContext('2d')
      setActiveControlPanelButton('Start')
      setStartButtonText('Pause')
      window.speechSynthesis.cancel()
      setCurrentSlideIndex(currentSlideIndex - 1)
      await nextSlide(ctx, canvas, currentSlideIndex - 1)
    }
  }

  return (
    <Container>
      <Row>
        <Col>
          <div className="slideControls">
            {
              currentSlideIndex > 0 && 
              (<Button style={{margin: "0px 0px 0px 10px"}} color={activeControlPanelButton === 'Prev' ? 'primary' : 'secondary'} onClick={onPrevClick} >
                Prev
              </Button>)
            }
            {
              (<Button style={{margin: "0px 0px 0px 10px"}} color={activeControlPanelButton === 'Start' ? 'primary' : 'secondary'} onClick={onStartClick} >
                {startButtonText}
              </Button>)
            }
            {
              currentSlideIndex + 1 < slides.length && 
              (<Button style={{margin: "0px 0px 0px 10px"}} color={activeControlPanelButton === 'Next' ? 'primary' : 'secondary'} onClick={onNextClick} >
                Next
              </Button>)
            }
          </div>
        </Col>
      </Row>
      <Row>
        <Col>
          <pre>{currentScriptSentence}</pre>
        </Col>
      </Row>
      <Row>
        <Col>
          <canvas 
            ref={canvasRef} 
            width="1000" 
            height="500" 
            className="canvas" 
            style={{
              opacity: opacity,
              transition: "opacity 0.1s linear"
            }}
          />
          <canvas ref={scratchCanvasRef} width="1000" height="500" className="canvas" hidden />
        </Col>
      </Row>
    </Container>
  )
}

export default Slides
