import { useQuery } from 'react-query'
import { Container, Row, Col } from 'reactstrap'
import { useParams } from 'react-router'

import { httpGet } from './utils/axiosUtils'
import ContentItem from './contentItem'

function UserCourseItem() {
  const params = useParams()
  const { isLoading, error, data } = useQuery({
    queryKey: ['course'],
    queryFn: async () => await httpGet(`userCourses/${params.courseId}`)
  })

  if (isLoading) return (<>{'Loading...'}</>)
  if (error) return (<>{'An error has occurred: ' + error}</>)

  const courseItem = data?.course?.contents?.find(( { _id }: any ) => {
    return _id === params.itemId
  })

  return (
    <Container>
      <Row>
        <h2>
          Course
        </h2>
      </Row>
      <Row>
        <Col>{data?.course?.title}</Col>
      </Row>
      <Row>
        <Col>{data?.course?.author}</Col>
      </Row>
      <Row>
        <Col>{courseItem?.title}</Col>
      </Row>
      <Row>
        <Col>
          <ContentItem contentItem={courseItem} />
        </Col>
      </Row>
    </Container>
  )
}

export default UserCourseItem
