import { useQuery } from 'react-query'
import { Card, CardBody, CardSubtitle, CardTitle, Container, Row, Col } from 'reactstrap'
import { Link } from 'react-router'

import { httpGet } from './utils/axiosUtils'

function UserCourseList() {
  const { isLoading, error, data } = useQuery({
    queryKey: ['course'],
    queryFn: async () => await httpGet(`userCourses`)
  })

  if (isLoading) return (<>{'Loading...'}</>)
  if (error) return (<>{'An error has occurred: ' + error}</>)

  const courseData = (
    data?.courses?.map(({ courseId, completedContentIds }: any) => {
      const { _id, title, author, subject, skillLevel, subCategory, thumbnails, contents } = courseId
      const percentComplete = !completedContentIds?.length || !contents ? 0 : Math.round(completedContentIds.length / contents.length * 100)
      return (
        <Col sm="4" key={_id}>
          <Card style={{width: '322px'}}>
            <img alt="Sample" src={thumbnails?.medium?.url} height={thumbnails?.medium?.height} width={thumbnails?.medium?.width} />
            <CardBody>
              <CardTitle tag="h5">
                {title}
              </CardTitle>
              <CardSubtitle>
                <div>{author}</div>
                <div>{subject}</div>
                <div>{subCategory}</div>
                <div>{skillLevel}</div>
              </CardSubtitle>
              <div>
                <Link to={`/userCourses/${_id}`}>{percentComplete === 0 ? 'Begin' : percentComplete === 100 ? 'Review' : 'Continue'}</Link> 
              </div>
              <div>
                {`${percentComplete}% Complete`}
              </div>
            </CardBody>
          </Card>
        </Col>
      )
    })
  )

  return (
    <Container>
      <Row>
        <h2>
          My Courses
        </h2>
      </Row>
      <Row>
        {courseData}
      </Row>
    </Container>
  )
}

export default UserCourseList
