import { useQuery } from 'react-query'
import { Card, CardBody, CardTitle, CardText, Container, Row, Col } from 'reactstrap'
import { useNavigate, useParams } from 'react-router'
import { Link } from 'react-router'

import { httpGet, httpPost } from './utils/axiosUtils'

function UserCourse() {
  const navigate = useNavigate()
  const params = useParams()
  const { isLoading, error, data } = useQuery({
    queryKey: ['course'],
    queryFn: async () => await httpGet(`userCourses/${params.courseId}`)
  })

  if (isLoading) return (<>{'Loading...'}</>)
  if (error) return (<>{'An error has occurred: ' + error}</>)

  const { completedContentIds } = data?.userCourse || {}
  const { _id, contents, title, author, description, summary } = data?.course || {}
  const completedContentIdsSet = new Set(completedContentIds)

  const onContentItemClick = async (contentId: string) => {
    await httpPost(`userCourses/${_id}/${contentId}/complete`, {})
    navigate(`/userCourses/${_id}/${contentId}`)
  }

  const courseContents = (
    contents?.map(({ _id, title, summary, description }: any) => {
      const isContentCompleted = completedContentIdsSet.has(_id)
      return (
        <Row key={title}>
          <Col>
            <Card style={{width: '100%'}}>
              <CardBody>
                <CardTitle tag="h5">
                  <Link to='' onClick={() => onContentItemClick(_id)}>{title}</Link>
                </CardTitle>
                <CardText>
                  {isContentCompleted && (<div>Completed</div>)}
                  <pre>{description}</pre>
                </CardText>
              </CardBody>
            </Card>
          </Col>
        </Row>
      )
    })
  )

  const percentComplete = !completedContentIds?.length || !contents ? 0 : Math.round(completedContentIds.length / contents.length * 100)
  return (
    <Container>
      <Row>
        <h2>
          User Course
        </h2>
      </Row>
      <Row>
        <Col>{title}</Col>
      </Row>
      <Row>
        <Col>{author}</Col>
      </Row>
      <Row>
        <Col>{`${percentComplete}% Complete`}</Col>
      </Row>
      <Row>
        <Col><pre>{summary}</pre></Col>
      </Row>
      <Row>
        <Col>{description}</Col>
      </Row>
      {courseContents}
    </Container>
  )
}

export default UserCourse
