import YouTubeVideo from './youTubeVideo'
import Slides from './slides'

function ContentItem({ contentItem }: any) {
  return (
    <>
      <div>
        <pre>{contentItem.summary}</pre>
      </div>
      <div>
        {(contentItem?.contentType === 'slides' && <Slides slides={contentItem?.slides} />)}
        {(contentItem?.contentType === 'video' && <YouTubeVideo videoId={contentItem?.videoId} />)}
      </div>
      <div>
        <pre>{contentItem?.description}</pre>
      </div>
    </>
  )
}

export default ContentItem
