import { useState } from 'react'
import { QueryClient, QueryClientProvider } from 'react-query'
import { BrowserRouter, Routes, Route } from 'react-router'

import './App.css'
import CourseList from './courseList'
import Course from './course'
import CourseItem from './courseItem'
import Header from './header'
import Footer from './footer'
import Home from './home'
import Login from './login'
import Oauth from './oauth'
import UserCourseList from './userCourseList'
import UserCourse from './userCourse'
import UserCourseItem from './userCourseItem'
import Upload from './upload'

function App() {
  const [isLoggedIn, setIsLoggedIn] = useState<boolean>(false)
  const queryClient = new QueryClient()

  const login = () => {
    setIsLoggedIn(true)
  }

  const logout = () => {
    setIsLoggedIn(false)
  }

  return (
    <>
      <QueryClientProvider client={queryClient}>
        <BrowserRouter>
          <Header isLoggedIn={isLoggedIn} onLogout={logout} />
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/courses" element={<CourseList />} />
            <Route path="courses/:courseId" element={<Course />} />
            <Route path="courses/:courseId/:itemId" element={<CourseItem />} />
            <Route path="/userCourses" element={<UserCourseList />} />
            <Route path="/userCourses/:courseId" element={<UserCourse />} />
            <Route path="/userCourses/:courseId/:itemId" element={<UserCourseItem />} />
            <Route path="/login" element={<Login />} />
            <Route path="/oauth" element={<Oauth onLogin={login} />} />
            <Route path="/upload" element={<Upload />} />
          </Routes>   
          <Footer />
        </BrowserRouter>
      </QueryClientProvider>
    </>
  );
}

export default App;
