import { Container, Nav, Navbar, NavbarBrand, NavItem } from 'reactstrap'
import { NavLink } from 'react-router'

import { httpGet } from './utils/axiosUtils'

function Header({ isLoggedIn, onLogout }: { isLoggedIn: boolean | undefined, onLogout: () => void }) {
  const logout = async () => {
    await httpGet('auth/logout')
    onLogout?.()
  }

  return (
    <Navbar dark color='primary' sticky='top' expand='md'>
        <Container>
            <NavbarBrand href='/'>
              Assessment
            </NavbarBrand>
            <Nav className="me-auto" navbar>
              <NavItem>
                <NavLink className='nav-link' to='/'>
                  Home
                </NavLink>
              </NavItem>
              {
                !isLoggedIn && 
                (
                  <>
                    <NavItem>
                      <NavLink className='nav-link' to='/login'>
                        Login
                      </NavLink>
                    </NavItem>
                  </>
                )
              }
              {
                isLoggedIn &&
                (
                  <>
                    <NavItem>
                      <NavLink className='nav-link' to='/courses'>
                        Courses
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink className='nav-link' to='/userCourses'>
                        My Courses
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink className='nav-link' to='/upload'>
                        Upload
                      </NavLink>
                    </NavItem>
                    <NavItem>
                      <NavLink className='nav-link' onClick={logout} to='/login'>
                        Logout
                      </NavLink>
                    </NavItem>
                  </>
                )
              }
            </Nav>
        </Container>
    </Navbar>
  )
}

export default Header
