import { Container, Row, Col, FormGroup, Label, Input, Button } from 'reactstrap'
import { useState, ChangeEvent } from 'react'
import * as pdfjsLib from 'pdfjs-dist'

import { httpPost } from './utils/axiosUtils'

pdfjsLib.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjsLib.version}/pdf.worker.min.mjs`

function Upload() {
  const [pdfText, setPdfText] = useState<any[]>([])
  const [imageText, setImageText] = useState<any[]>([])
  const [maxChapter, setMaxChapter] = useState<number>(0)
  const [title, setTitle] = useState<string>('Delftse Foundations of Computation')
  const [description, setDescription] = useState<string>('Delftse Foundations of Computation')
  const [author, setAuthor] = useState<string>('Stefan Hugtenburg and Neil Yorke-Smith')
  const [includeImageText, setIncludeImageText] = useState<boolean>(false)
  const [hasLeftAndRightMargins, setHasLeftAndRightMargins] = useState<boolean>(true)
  const [firstPage, setFirstPage] = useState<number>(10)
  const [lastPage, setLastPage] = useState<number>(173)
  const [courseId, setCourseId] = useState<string>('')

  const calculatePercentile = (data: number[], percentile: number) => {
    if (percentile < 0 || percentile > 100) {
      throw new Error("Percentile must be between 0 and 100");
    }

    // Sort the data in ascending order
    data.sort((a: number, b: number) => a - b);

    // Calculate the rank
    const index = (percentile / 100) * (data.length - 1);

    // If the index is an integer, return the value at that position
    if (Number.isInteger(index)) {
        return data[index];
    }

    // Otherwise, interpolate between the nearest values
    const lower = Math.floor(index);
    const upper = Math.ceil(index);
    const weight = index - lower;

    return data[lower] + weight * (data[upper] - data[lower]);
  }

  const isUnicodeCharacter = (str: string) => {
    return str.length === 1 && str.charCodeAt(0) > 127 && str.toLowerCase() !== str.toUpperCase()
  }

  const getPageTextItems = (page: any, pageNumber: number, text: any) => {
    let textArray: { str: any; x: any; y: any, fontSize: any, page: any }[] = []
    const viewport = page.getViewport({ scale: 1 })
    const maxY = viewport.height
    const textsToIgnore = ['•', '', '\uF071']
    let firstTextOfPage = true

    text.items.forEach((item: any) => {
      const [x, y] = viewport.convertToViewportPoint(item.transform[4], item.transform[5])
      const text = item.str
      const transformY = maxY - y
      const fontSize = Math.sqrt(item.transform[0] ** 2 + item.transform[1] ** 2)
      if (text.replace(' ', '') && !textsToIgnore.includes(text) && !isUnicodeCharacter(text)) {
        textArray.push({
          ...item,
          str: text,
          x,
          y: transformY,
          startY: transformY + item.height,
          fontSize,
          firstTextOfPage,
          page: pageNumber
        })
        firstTextOfPage = false
      }
    })

    // remove margin text based on percentile
    if (hasLeftAndRightMargins) {
      const lastPageTextXs = textArray.map(({ x }) => x)
      const lastXPercentile = calculatePercentile(lastPageTextXs, 99.5)
      const firstXPercentile = calculatePercentile(lastPageTextXs, 0.5)
      textArray = textArray.filter(({ x }) => x > firstXPercentile && x < lastXPercentile)
    }

    return textArray
  }

  const getPageImageItems = async (page: any, canvas: any, ctx: any, pageNumber: number, imageCounter: number) => {
    const imageArray: { str: string; x: any; y: any, page: any, height: number, width: number, base64: any }[] = []
    const operatorList = await page.getOperatorList()
    let currentTransform = [1, 0, 0, 1, 0, 0]
    for (let operatorCounter = 0; operatorCounter < operatorList.fnArray.length; operatorCounter++) {
      const fn = operatorList.fnArray[operatorCounter]
      const args = operatorList.argsArray[operatorCounter]

      if (fn === pdfjsLib.OPS.transform) {
        currentTransform = args
      } else if (fn === pdfjsLib.OPS.paintImageXObject || 
        fn === pdfjsLib.OPS.paintInlineImageXObject
      ) {
        const imageName = args[0]
        try {
          const image: any = await waitForImage(page, imageName)
          if (image) {
            let base64Image: string
            let height: number
            let width: number
            if (image.bitmap instanceof ImageBitmap) {
              canvas.width = image.bitmap.width
              canvas.height = image.bitmap.height
              ctx.drawImage(image.bitmap, 0, 0)
              height = image.bitmap.height
              width = image.bitmap.width
              base64Image = canvas.toDataURL()
            } else {
              base64Image = image.src
              height = image.src.height
              width = image.src.width
            }

            const [_scaleX, _skewY, _skewX, _scaleY, transformX, transformY] = currentTransform
            const imageProperties = {
              str: `image:${imageCounter}`,
              x: transformX,
              y: transformY,
              page: pageNumber,
              height,
              width,
              base64: base64Image,
            }
            currentTransform[5] = currentTransform[5] + args[2]
            imageArray.push(imageProperties)
            imageCounter++
          }
        } catch (e) {}
      }
    }

    return imageArray
  }

  const addChaptersAndSections = (pdfItems: any[]) => {
    const items: any[] = []
    let currentChapter = 0
    let currentSection = 0
    let currentTextHeight = 0
    let currentPage = 0
    let firstTextOfPage = true
    let newChapter = false
    const sectionsToIgnore = ['Exercises']
    let ignoreSection = false

    pdfItems.forEach((item: any) => {
      if (item.page !== currentPage) {
        firstTextOfPage = true
        currentPage = item.page
      } else {
        firstTextOfPage = false
      }

      const text = item.str
      if (firstTextOfPage && 
        text.substring(0, 1) === (currentChapter + 1).toString() || 
        text.trim().startsWith(`Chapter ${currentChapter + 1}`)
      ) {
        currentChapter++
        currentSection = 0
        newChapter = true
      } else {
        newChapter = false
      }

      if (item.fontSize > 9) {
        if ((item.height >= currentTextHeight + 1.5 || item.height < currentTextHeight && newChapter) && text.length > 1) {
          ignoreSection = sectionsToIgnore.includes(item.str)
          if (!ignoreSection) {
            console.log(item.str, text.length, text, item.height, currentTextHeight, currentSection, currentChapter, item.page)
            currentSection++
          }

          currentTextHeight = item.height
        } else if (item.height < currentTextHeight) {
          currentTextHeight = item.height
        }

        if (currentChapter > 0 && !ignoreSection) {
          items.push({
            ...item,
            chapter: currentChapter,
            section: currentSection
          })
        }
      }
    })

    setMaxChapter(currentChapter)
    return items
  }

  const mergePdfItems = (textArray: any[], imageArray: any[]) => {
    const textArrayWithType = textArray.map((item) => ({ ...item, type: 'text' }))
    const imageArrayWithType = imageArray.map((item) => ({ ...item, type: 'image' }))
    const mergedArray = [...textArrayWithType, ...imageArrayWithType]
    mergedArray.sort((a, b) => {
        if (a['page'] < b['page'] || (a['page'] === b['page'] && a['startY'] > b['startY']) || (a['page'] === b['page'] && a['startY'] === b['startY']) && a['x'] < b['x']) {
            return -1
        } else if (a['page'] > b['page'] || (a['page'] === b['page'] && a['startY'] < b['startY']) || (a['page'] === b['page'] && a['startY'] === b['startY'] && a['x'] > b['x'])) {
            return 1
        } else {
            return 0
        }
    })

    let currentChapter = 0
    let currentSection = 0
    for(const item of mergedArray) {
      currentChapter = item.type === 'text' ? item.chapter : currentChapter
      currentSection = item.type === 'text' ? item.section : currentSection
      if (item.type === 'image') {
        item.chapter = currentChapter
        item.section = currentSection
      }
    }

    return mergedArray
  }

  const readPdf = async (file: any) => {
    const canvas: any = document.getElementById("canvas");
    const ctx = canvas?.getContext("2d")
    let imageCounter = 1
    let textItems: any[] = []
    const imageItems: any[] = []

    const fileReader = new FileReader()
    fileReader.onload = async (e) => {
      if (e.target?.result) {
        try {
          const pdfData = new Uint8Array(e.target?.result as ArrayBuffer)
          const pdfDoc = await pdfjsLib.getDocument({ data: pdfData }).promise
          for (let i = 1; i <= pdfDoc.numPages; i++) {
            if (i > firstPage && i <= lastPage) {
              const page = await pdfDoc.getPage(i)
              const text = await page.getTextContent()

              const pageTextItems = getPageTextItems(page, i, text)
              const pageImageItems = await getPageImageItems(page, canvas, ctx, i, imageCounter)
              
              textItems.push(...pageTextItems)
              imageItems.push(...pageImageItems)
            }
          }

          // remove headers and footers
          const textYs = textItems.map(({ y }) => y)
          const lastYPercentile = calculatePercentile(textYs, 99.5)
          const firstYPercentile = calculatePercentile(textYs, 0.5)
          textItems = textItems.filter(({ y }) => y > firstYPercentile && y < lastYPercentile)

          const textItemsWithChaptersAndSections = addChaptersAndSections(textItems)
          const mergedPdfItems = mergePdfItems(textItemsWithChaptersAndSections, imageItems)
          setPdfText(mergedPdfItems.filter(({ type }) => type === 'text'))
          setImageText(mergedPdfItems.filter(({ type }) => type === 'image'))
        } catch (err) {
          console.error(err)
        }
      }
    }

    fileReader.readAsArrayBuffer(file)
  }

  const onPdfUploadChange = async (event: ChangeEvent<HTMLInputElement>) => {
    const file = event?.target?.files?.[0]
    if (file && file.type === "application/pdf") {
      await readPdf(file)
    } else {
      alert("Please upload a valid PDF file.")
    }
  }

  async function waitForImage(page: any, imageName: string) {
    return new Promise((resolve, reject) => {
        const timeout = 5000; // Timeout in ms
        const startTime = Date.now();

        const checkResolved = () => {
            if (page.objs.has(imageName)) {
                resolve(page.objs.get(imageName));
            } else if (Date.now() - startTime > timeout) {
                reject(new Error(`Timeout resolving image ${imageName}`));
            } else {
                setTimeout(checkResolved, 50); // Retry every 50ms
            }
        };

        checkResolved();
    });
  }

  const onUploadClick = async () => {
    let courseIdToSend = courseId
    for (let chapterCounter = 1; chapterCounter < maxChapter +1; chapterCounter++) {
      console.log('start', chapterCounter)
      const chapterText = pdfText.filter((item) => item.chapter === chapterCounter)
      const chapterImages = imageText.filter((item) => item.chapter === chapterCounter)
      try {
        const course = await httpPost(`courses/slides`, { 
          uploadType: 'text', 
          text: JSON.stringify(chapterText), 
          images: JSON.stringify(chapterImages),
          chapter: chapterCounter,
          courseId: courseIdToSend,
          title: title,
          description: description,
          author: author,
          options: { includeImageText }
        })
        courseIdToSend = course._id
        setCourseId(course._id)
      } catch (e) {}
      console.log('stop')
    }
  }

  return (
    <Container>
      <Row>
        <Col>
          <h2>
            Upload
          </h2>
        </Col>
      </Row>
      <Row>
        <Col>
          <FormGroup>
            <Label for="pdfUpload">
              File
            </Label>
            <Input
              id="pdfUpload"
              name="file"
              type="file"
              accept="application/pdf"
              onChange={onPdfUploadChange}
            />
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <FormGroup>
            <Label for="title">
              Title
            </Label>
            <Input
              id="title"
              name="title"
              type="text"
              vaue={title}
              defaultValue={title}
              onChange={(e) => setTitle(e.target.value)}
            />
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <FormGroup>
            <Label for="description">
              Description
            </Label>
            <Input
              id="description"
              name="description"
              type="text"
              vaue={description}
              defaultValue={description}
              onChange={(e) => setDescription(e.target.value)}
            />
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <FormGroup>
            <Label for="author">
              Author
            </Label>
            <Input
              id="author"
              name="author"
              type="text"
              vaue={author}
              defaultValue={author}
              onChange={(e) => setAuthor(e.target.value)}
            />
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <FormGroup>
            <Label for="firstPage">
              First Page
            </Label>
            <Input
              id="firstPage"
              name="firstPage"
              type="number"
              defaultValue={firstPage}
              vaue={firstPage}
              onChange={(e) => setFirstPage(Number(e.target.value))}
            />
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <FormGroup>
            <Label for="lastPage">
              Last Page
            </Label>
            <Input
              id="lastPage"
              name="lastPage"
              type="number"
              defaultValue={lastPage}
              vaue={lastPage}
              onChange={(e) => setLastPage(Number(e.target.value))}
            />
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <FormGroup>
            <Label for="includeImageText">
              Include Text for Images
            </Label>
            <div>
              <Input
                id="includeImageText"
                name="includeImageText"
                type="checkbox"
                checked={includeImageText}
                onChange={(e) => setIncludeImageText(e.target.checked)}
              />
            </div>
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <FormGroup>
            <Label for="hasLeftAndRightMargins">
              Has Left and Right Margins
            </Label>
            <div>
              <Input
                id="hasLeftAndRightMargins"
                name="hasLeftAndRightMargins"
                type="checkbox"
                checked={hasLeftAndRightMargins}
                onChange={(e) => setHasLeftAndRightMargins(e.target.checked)}
              />
            </div>
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <FormGroup>
            <Label for="courseId">
              Course ID
            </Label>
            <Input
              id="courseId"
              name="courseId"
              type="text"
              vaue={courseId}
              onChange={(e) => setCourseId(e.target.value)}
            />
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <pre>{pdfText.map((item: any) => `
            Chapter: ${item.chapter} 
            Section: ${item.section} 
            Page: ${item.page} 
            Text: '${item.str}'
            Font: ${item.fontSize}
            X: ${item.x}
            Y: ${item.y}
          `).join('\n')}</pre>
          <pre>{JSON.stringify(imageText)}</pre>
        </Col>
      </Row>
      <Row>
        <Col>
          <Button color="primary" onClick={onUploadClick} >
            Upload
          </Button>
        </Col>
      </Row>
      <Row>
        <Col>
          <canvas id="canvas" />
          <div id="output"></div>
        </Col>
      </Row>
    </Container>
  )
}

export default Upload
